import {
  Button,
  Divider,
  TextField,
  Tooltip,
  Typography,
  Paper,
  Alert,
  AlertTitle,
  Box,
  Grow,
  Zoom,
} from "@mui/material";
import React, { useState } from "react";
import { Login as LoginIcon } from "@mui/icons-material";
import imageOnTop from "../assets/img/itmp-1.png";
import { useRegistration } from "../Store";

export const NoRegistration = () => {
  const [registration, loading, error] = useRegistration();
  const [id, setId] = useState("");

  const login = (e: React.FormEvent) => {
    e.preventDefault();
    window.location.href =
      window.location.origin + window.location.pathname + "?q=" + id;
  };

  return (
    <Box
      sx={{
        height: "100%",
        overflowY: "auto",
        backgroundColor: "primary.main",
        p: { xs: 2, md: 6 },
      }}
    >
      <Paper
        elevation={10}
        sx={{
          margin: "auto",
          bgcolor: "primary.main",
          p: 4,
          color: "text.primary",
          borderRadius: "8px",
          textAlign: "center",
          width: "600px",
          maxWidth: "100%",
          fontWeight: "600",
        }}
      >
        {error && (
          <Zoom in>
            <Alert severity="error" sx={{ mb: 4, textAlign: "left", pr: 3 }}>
              <AlertTitle
                sx={{ pt: "3px", fontWeight: 700, textAlign: "left" }}
              >
                rvnytelen kd
              </AlertTitle>
              A link, amire kattintottl, vagy az ltalad megadott kd
              rvnytelen.
              <br />
              Amennyiben mr regisztrltl, kattints az e-mailben kapott linkre,
              vagy add meg az oldal aljn az egyedi kdodat.
              <br />
              Ha mg nem regisztrltl, akkor kattints az albbi "Regisztrci"
              gombra.
            </Alert>
          </Zoom>
        )}
        <Typography variant="h5" sx={{ fontWeight: "bold", mb: 4 }}>
          Az IOK 2024 konferencin val rszvtel regisztrcihoz kttt!!
        </Typography>
        <img
          src={imageOnTop}
          alt=""
          style={{ width: "200px", maxWidth: "70%" }}
        />
        <Typography variant="body1" component="p" sx={{ my: 1 }}>
          Regisztrlj, s emailben megkapod a bejelentkezshez hasznlhat
          szemlyes linkedet s a szemlyes kdodat!
        </Typography>

        <Button
          variant="contained"
          color="secondary"
          sx={{ mt: 2 }}
          size="large"
          href="https://iok2024.http-alapitvany.hu/"
        >
          Regisztrci
        </Button>

        <Divider color="#fff" sx={{ my: 4 }} />
        <Typography
          variant="body1"
          component="p"
          sx={{ fontSize: "0.9rem", mb: 1, mt: 2 }}
        >
          Amennyiben mr regisztrltl, kattints az e-mailben kapott linkre,
          vagy add meg az egyedi kdodat:
        </Typography>
        <form onSubmit={login}>
          <TextField
            value={id}
            onChange={(e) => setId(e.target.value)}
            placeholder="Egyedi kd"
            sx={{
              width: "300px",
              input: { textAlign: "center" },
              fieldset: { borderColor: "#fff !important" },
            }}
          />
          <Tooltip title="Belps" arrow placement="right">
            <Button
              type="submit"
              variant="contained"
              color="secondary"
              sx={{ ml: 1, mt: "6px", height: "49px" }}
            >
              <LoginIcon sx={{ transform: "translateX(-2px)" }} />
            </Button>
          </Tooltip>
        </form>
      </Paper>
    </Box>
  );
};
